
.contact-container {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	padding-top: 8rem;
}

.socials-container {
	display: flex;
	flex-direction: column;
	margin-top: 5rem;
}

.contact-socials {
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.contact-subtitle a {
	color: var(--link-color);
	text-decoration: none;
}
