.not-found {
	overflow: hidden;
	height: 100vh;
}

.notfound-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 8rem;
}

.notfound-message {
	position: fixed;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	top: 30%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.notfound-title {
	font-family: var(--secondary-font);
	color: var(--primary-color);
	font-size: var(--font-size-max);
    padding-top: 1rem;
	font-weight: 700;
}

.not-found-message {
	color: var(--secondary-color);
	margin: 2rem 0;
	text-align: center;
}

.not-found-link {
	text-decoration: none;
	color: var(--white-color);
	background-color: var(--link-color);
	padding: 1rem 2rem;
    border-radius: 3rem;
	transition: all 0.3s ease-in-out;
}

.not-found-link:hover {
	background-color: var(--white-color);
	color: var(--link-color);
	box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
}

@media (max-width: 768px) {
	.notfound-message {
		position: absolute;
		top:20%;
		left: 0;
        right: 0;
        width: 100%;
		transform: none;
	}

	.notfound-title {
		font-size: var(--font-size-pro);
	}

	.not-found-message {
		width: 100%;
		margin-top: 1rem;
	}

	.not-found-link {
		padding: 1rem;
	}
}
