::-webkit-scrollbar {
	width: 10px;
}

::-webkit-scrollbar-track {
	background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
	background-color: #383636;
	border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
	background-color: rgb(0, 0, 0);
}

.page-content {
	background-color: white;
	position: relative;
	border: 1px solid #f4f4f5;
	margin: 0 4rem;
	padding: 2rem 5rem 2rem ;
}

.content-wrapper {
	margin: 0 auto;
}

.title {
	color: var(--primary-color);
	font-family: var(--secondary-font);
	font-size: var(--font-size-max);
	font-weight: 700;
}

.subtitle {
	padding-top: 1rem;
	color: var(--secondary-color);
	margin-top: 2rem;
}

.page-footer {
	display: block;
}

.page-footer::before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	border-top: 2px solid var(--quaternary-color);
}

@media (max-width: 1024px) {
	.page-content {
		margin: 0 2rem;
		padding: 2rem 2rem;
	}
	.content-wrapper {
		margin: 0 auto;
		padding: 0 1rem;
	}
	.title {
		font-size: var(--font-size-pro);
	}
}

@media (max-width: 768px) {
	.page-content {
		margin: 0;
		padding: 2rem 2rem;
		border: 0px;
	}
	.content-wrapper {
		margin: 0 auto;
		padding: 0;
	}

	.title {
		font-size: var(--font-size-large);
	}
}