.footer {
	display: flex;
	padding-top: 2rem;
    margin-top: 3rem;
    padding-bottom: 2rem;
	align-items: center;
	justify-content: space-between;
	font-size: var(--font-size-small);
}

.footer-links {
	display: flex;
    list-style: none;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    padding: 0;
}

.footer-links a {
	text-decoration: none;
	color: var(--secondary-color);
}

.footer-links a:hover {
	color: var(--link-color);
}

.footer-credits-text {
	color: var(--grey-color);
}

@media (max-width: 768px) {
	.footer {
		flex-direction: column;
	}

	.footer-credits{
		margin-top: 2rem;
	}
}
